%form_el_label {
	display: block;
	margin-bottom: .4rem;

	font-size: 1.8rem;
	color: var(--black_color);
}

%form_el_default {
	display: block;
	width: 100%;
	padding: $input_offset;
	font-family: $biotif_font;
	color: $input_text_color;
	box-shadow: inset 0 0 0 1px $input_border_color;
	border: none;
	border-radius: $input_radius;

	@include bp($point_4, min) {
		font-size: $input_font_size;
		height: $input_height;
	}
	@include bp($point_4 - 1) {
		font-size: 1.6rem;
		height: 5.4rem;
	}

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

	.subscription_form__input & {
		padding-right: 7rem;
	}

	.subscription_form__field--error_state & {
		&::-webkit-input-placeholder {
			color: var(--orange_color);
		}

		&::-moz-placeholder {
			color: var(--orange_color);
		}

		&:-ms-input-placeholder {
			color: var(--orange_color);
		}
	}

	// &:hover,
	// &:focus {
	// 	box-shadow: inset 0 0 0 1px $input_border_color_active;
	// }

	-webkit-appearance: none;
}
