//---------------------------------------------ohter_icons
.icon {
	display: block;

	fill: currentColor;

	&.icon--size_mod {
		width: 100%;
		height: 100%;
	}

	&.icon_heart {
		position: absolute;

		@include bp($point_2, min) {
			top: -2.2rem;
			left: 1.4rem;
			height: 3.3rem;
			width: 3.8rem;
		}

		@media (min-width: $point_4) and (max-width: $point_3) {
			top: -2.2rem;
			left: 1.4rem;
			height: 3.3rem;
			width: 3.8rem;
		}

		@include bp($point_4 - 1) {
			top: -1.6rem;
			left: 1rem;
			height: 2.3rem;
			width: 2.8rem;
		}
	}

	.menu_link__icon--offset_1_mod & {
		@include bp($point_4 - 1) {
			margin-left: -.2rem;
		}
	}
	.menu_link__icon--offset_2_mod & {
		@include bp($point_4 - 1) {
			margin-left: -.6rem;
		}
	}
	.menu_link__icon--offset_3_mod & {
		@include bp($point_4, min) {
			margin-left: 1rem;
		}
	}
}
