@import "ScssHelpers/_helpers_all.scss";

.menu {
	position: fixed;
	bottom: 100%;
	left: 0;

	width: 100%;
	max-height: 100vh;

	overflow-y: auto;

	background-color: var(--menu-bg);
	transform: translateY(0%);

	@include z-index(menu);

	.body--active_menu & {
		transform: translateY(100%);

		@include bp($point_4, min) {
			transition: transform 0.6s ease-out;
		}
		@include bp($point_4 - 1) {
			transition: transform 0.4s ease-out, opacity 0.4s ease;
			opacity: 1;
		}
	}

	@include bp($point_2, min) {
		padding: 16.4rem var(--gl-indent) 15.6rem;
	}
	@include bp($point_3, $point_4) {
		padding: 12rem var(--gl-indent) 12rem;
	}

	@include bp($point_4, min) {
		transition: transform 0.4s ease-in;
	}
	@include bp($point_4 - 1) {
		min-height: 100vh;
		padding: calc(var(--height-header) + var(--height-cookie-mob)) 0 0;

		opacity: 0;

		transition: transform 0.3s ease-in, opacity 0.6s ease;
	}

	.body--mobile_cookie & {
		@include bp($point_4 - 1) {
			padding: var(--height-header) 0 0;
		}
	}
}

.menu_in {
	max-width: 127.8rem;
	width: 100%;
	margin: 0 auto;

	opacity: 0;

	transform: translateY(-2rem);

	@include bp($point_4, min) {
		transition: opacity 0.3s ease, transform 0.6s ease;
	}
	@include bp($point_4 - 1) {
		transition: opacity 0.1s ease, transform 0.4s ease;
	}

	.body--active_menu & {
		opacity: 1;
		transform: translateY(0);

		@include bp($point_4, min) {
			transition: opacity 0.3s ease 0.74s, transform 0.8s ease 0.5s;
		}
		@include bp($point_4 - 1) {
			transition: opacity 0.4s ease 0.4s, transform 0.6s ease 0.3s;
		}
	}
}

.menu_list {
	@include bp($point_2, min) {
		gap: 17.8rem 3vw;
	}
	@include bp($point_3, $point_4) {
		gap: 12rem 3vw;
	}
	@include bp($point_4, min) {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
	}
}

.menu_item {
	@include bp($point_4 - 1) {
		width: 100%;

		border-bottom: 1px solid #383c65;
	}
}

.menu_item__in {
	text-align: center;
}

.menu_link {
	margin: 0 auto;

	color: var(--white_color);

	transition: color 0.3s ease;

	&:hover,
	&:focus {
		color: var(--primary_light_color);
		text-decoration: none;
	}

	@include bp($point_4, min) {
		display: inline-block;
	}
	@include bp($point_4 - 1) {
		height: 100%;
		min-height: 8.2rem;
		padding: 2rem 5rem;

		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.menu_link__icon {
	flex-shrink: 0;
	display: block;

	@include bp($point_2, min) {
		width: 11.4rem;
		height: 8.4rem;
		margin: 0 auto 5.5rem;
	}
	@include bp($point_3, $point_4) {
		width: 9.2rem;
		height: 6.8rem;
		margin: 0 auto 3rem;
	}
	@include bp($point_4 - 1) {
		width: 4.2rem;
		height: 3.2rem;
		margin-right: 2.8rem;
	}
}

.menu_link__title {
	display: block;

	line-height: 1.23;
	font-weight: 300;
	color: inherit;
	letter-spacing: -0.02em;

	@include bp($point_2, min) {
		font-size: 3rem;
	}
	@include bp($point_3, $point_4) {
	}
	@include bp($point_4 - 1) {
		text-align: left;
	}
}
