@import "ScssHelpers/helpers_all";

.hero {
	position: relative;
	padding: var(--height-header) var(--gl-indent);

	&:before {
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;
		display: block;
		width: 100vw;
		height: 100%;

		background: var(--primary_dark_color);
		content: "";
	}
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);
		min-height: var(--full-screen);
	}

	@include bp($point_3) {
		@include flex_block(row, nowrap, flex-start, center);
		min-height: calc(var(--full-screen) - var(--height-header));

		padding-bottom: 14.4rem;
	}
}

.hero_content {
	@extend %global_flex_block_column_nowrap_center;

	@include bp($point_2, min) {
		margin-top: 13rem;
	}

	@include bp($point_2, min) {
		//padding: var(--height-header) 6rem 6rem 0;
		padding-right: 6rem;
		width: 50%;
	}

	@include bp($point_4 - 1) {
	}
}

.hero__title {
	@extend %h1;

	color: var(--white_color);

	@include bp($point_3, $point_4) {
		line-height: 0.94;
	}
}

.hero__word {
	@extend %global_flex_block_row_nowrap_flex-start;
	@extend %h1;

	position: relative;

	width: 100%;
	opacity: 0;

	@include bp($point_2, min) {
		margin-top: -1.4rem;
		margin-bottom: 6.4rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 1.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2.4rem;
	}
}

.hero__word_in {
	@extend %global_flex_block_row_nowrap_flex-start;
	overflow: hidden;
	width: 100%;

	&:nth-child(n + 2) {
		position: absolute;
		top: 0;
		left: 0;
	}

	&:nth-child(4n + 1) {
		color: var(--primary_light_color);
	}

	&:nth-child(4n + 2) {
		color: var(--dot-color);
	}

	&:nth-child(4n + 3) {
		color: var(--orange_color);
	}

	&:nth-child(4n + 4) {
		color: var(--green_color);
	}
}

.hero__word_in_text {
	line-height: 1.25;
	overflow: hidden;
}

.hero__word_in_text_in {
	display: block;
}

.hero__word_in_dot {
	font-family: $font_2;
	color: var(--white_color);
	line-height: 1;

	@media not all and (min-resolution: 0.001dpcm) {
		@supports (-webkit-appearance: none) {
			line-height: 1.25;
		}
	}
}

.hero__word_point {
	width: 0.12em;
	height: 0.12em;
	background: var(--white_color);
}

.hero__descrip {
	@extend %base_text_1;

	p {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
		a {
			&:hover,
			&:focus {
				color: var(--orange_color);
				text-decoration: none;
			}
		}
	}

	a {
		text-decoration: none;

		&:hover,
		&:active,
		&:focus {
			color: var(--orange_color);
			text-decoration: none;
		}
	}

	&.hero__descrip {
		color: var(--gray_2_color);
	}

	@include bp($point_2, min) {
		max-width: 35.6rem;
	}

	@include bp($point_3, $point_4) {
		line-height: 1.45;
		max-width: 19rem;
	}

	@include bp($point_3) {
		max-width: 94%;
	}
}

.hero_label {
	position: absolute;
	bottom: -1px;

	cursor: pointer;

	@include bp($point_2, min) {
		max-width: 23.2rem;
		right: calc(50vw - 1px);
		padding: 4.4rem 4.4rem 5.4rem 4rem;
	}

	// @include bp($point_3, $point_4) {
	// 	max-width: 12.6rem;
	// 	right: calc(50vw - 1px);
	// 	padding: 2rem 3.8rem 1.6rem 2rem;
	// }

	@include bp($point_3) {
		@include flex_block(row, nowrap, flex-start, center);

		left: 0;
		padding: 1.4rem var(--gl-indent);
		width: 100%;
		min-height: 7.2rem;
	}

	&:hover,
	&:active,
	&:focus {
		text-decoration: none;
	}

	&:hover:before {
		@include bp($point_2, min) {
			transform: scale(1.1);
		}

		// @include bp($point_4 - 1) {
		// 	transform: scale(1.2);
		// }
	}

	&::before {
		@extend %transition_transform;
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;
		background: var(--white_color);
		content: "";

		transform-origin: right bottom;
	}
}

.hero_label__in {
	position: relative;

	@include bp($point_3) {
		@include flex_block(row, nowrap, space-between, center);
		flex: auto;
	}
}

.hero_label__img {
	position: relative;

	display: block;

	@include bp($point_2, min) {
		width: 4.2rem;
		height: 4.2rem;
		margin-bottom: 3.8rem;
	}

	// @include bp($point_3, $point_4) {
	// 	width: 2.4rem;
	// 	height: 2.4rem;
	// 	margin-bottom: 1.2rem;
	// }

	@include bp($point_3) {
		flex-shrink: 0;
		width: 2.8rem;
		height: 2.8rem;
		margin-right: 3rem;
	}

	.hero_label:hover & {
		animation: 1.2s ease-in-out 0s normal none infinite running trambling-animation;
	}
}

.hero_label__img_in {
	@extend %cover_img;
}

.hero_label__text {
	@extend %transition_color;
	display: block;
	font-weight: 700;
	line-height: 1.35;
	letter-spacing: normal;
	text-align: left;

	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.3rem;
	}

	@include bp($point_4 - 1) {
		padding-right: 7rem;
		font-size: 1.4rem;
	}

	.hero_label:hover & {
		color: var(--color_link_base);
	}
}

.hero_label__btn {
	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_3) {
		display: inline-flex;
		justify-content: center;
		align-items: center;
		flex-shrink: 0;

		margin-right: 1.4rem;
		width: 3.2rem;
		height: 3.2rem;

		background-color: rgba(#eef3f9, 0.75);
		color: var(--primary_dark_color);
		border-radius: 50%;

		cursor: pointer;
		transition: background-color 0.3s ease;
	}

	.hero_label:hover & {
		background-color: transparent;
	}
}

.hero_label__btn_in {
	width: 0.6rem;
	height: 1rem;
}

.hero_video {
	position: absolute;
	top: 0;
	right: 0;

	width: 50vw;
	height: 100%;

	background: url("../images/hero/video_placeholder.jpg") no-repeat center center;
	background-size: cover;

	@include bp($point_3) {
		display: none;
	}
}

.hero_video__in {
	@extend %cover_img;

	opacity: 0;
	transition: opacity 0.3s;

	&:nth-child(n + 2) {
		position: absolute;
		top: 0;
		left: 0;
	}

	&.hero_video__in--active {
		opacity: 1;
		z-index: 2;
	}
}

@keyframes trambling-animation {
	0%,
	50%,
	100% {
		transform: rotate(0deg);
	}
	10%,
	30% {
		transform: rotate(-10deg);
	}
	20%,
	40% {
		transform: rotate(10deg);
	}
}
