@import "ScssHelpers/_helpers_all.scss";

.cookie {
	position: fixed;
	bottom: 0;
	left: 0;

	background-color: var(--primary_color);
	transition: opacity ease-in-out 0.3s;

	@include z-index(cookie_bar);

	@include bp($point_2, min) {
		min-height: 12.8rem;
		width: 100vw;

		padding-top: 3.2rem;
		padding-bottom: 3.2rem;
	}

	@include bp($point_3) {
		min-height: var(--height-cookie-mob);

		padding-top: 2rem;
		padding-bottom: 2rem;
	}

	@include bp($point_4 - 1) {
		bottom: auto;
		top: 0;
	}

	// &.cookie--active_mod {
	// 	top: 0;
	// 	bottom: auto;
	// }

	&.cookie--close_mod,
	&.cookie--accept_mod {
		opacity: 0;
		pointer-events: none;
	}

	.body--mobile_cookie & {
		transition: transform ease 0.3s;

		// @include bp($point_3) {
		// 	animation: cookie ease forwards .3s;
		// }
	}
}

