.cookie__row {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin-left: auto;
	max-width: 140rem;
}

.cookie__title,
.cookie__title_mob {
	font-weight: 400;
	color: var(--white_color);
}

.cookie__title_mob {
	font-size: 1.4rem;

	@include bp($point_2, min) {
		display: none;
	}
}

.cookie__title {
	font-size: 2rem;

	@include bp($point_3) {
		display: none;
	}
}

.cookie__title_w {
	position: relative;

	max-width: 75.5rem;
	width: 100%;

	font-size: 2rem;
	color: var(--white_color);

	@include bp($point_2, min) {
		padding-left: 11rem;
		padding-right: 4rem;
	}

	@include bp($point_3) {
		padding-left: 7rem;
		padding-right: 2rem;
	}
}

.cookie__img {
	position: absolute;
	left: 0;

	@include bp($point_2, min) {
		top: 0;
		width: 8.4rem;
		height: 5rem;
	}

	@include bp($point_3 - 1) {
		top: 50%;

		width: 5rem;
		height: 2.8rem;

		transform: translateY(-50%);
	}
}

.cookie__img_in {
	@extend %cover_img;
}

.cookie__btns_w {
	@include flex_block(row, nowrap, space-between, center, center);
	margin-left: auto;
	max-width: 52.4rem;
	flex-grow: 1;
}

.cookie__btn_accept_mob {
	color: var(--white_color);
	cursor: pointer;
	transition: color ease-in-out 0.3s;
	margin-left: auto;

	&:hover {
		color: rgba(#fff, 0.7);
	}

	@include bp($point_2, min) {
		display: none;
	}

	@include bp($point_2, min) {
		width: 5.2rem;
		height: 5.2rem;
	}

	@include bp($point_3) {
		width: 3.2rem;
		height: 3.2rem;
		margin-right: 1rem;
	}
}

.cookie__btn_accept {
	@extend %btn_theme_5;
	@extend %btn_size_2;

	@include bp($point_3) {
		display: none;
	}
}

.cookie__btn_close {
	color: var(--white_color);
	padding: 1rem;
	width: 3.8rem;
	height: 3.8rem;
	// width: 1.8rem;
	// height: 1.8rem;

	cursor: pointer;
	transition: color ease-in-out 0.3s;

	&:hover {
		color: rgba(#fff, 0.7);
	}

	@include bp($point_3) {
		display: none;
	}
}
