@import "ScssHelpers/_helpers_all.scss";

.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;

	font-size: 1rem;

	will-change: transform;
	background-color: var(--primary_dark_color);

	@include bp($point_2, min) {
		padding: 12.4rem var(--gl-indent) 13rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		padding: 7.4rem var(--gl-indent) 7.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 4rem var(--gl-indent) 6.2rem;
	}
}

.footer__row_bottom,
.footer__row_top {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		margin: 0 -4rem -4rem 0;
	}
}

.footer__row_top {
	@include bp($point_2, min) {
		padding-bottom: 13.4rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		margin-bottom: 6.4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 4.4rem;
	}
}

.footer__col {
	@include bp($point_2, min) {
		padding: 0 4rem 4rem 0;

		&:first-child {
			width: 65%;
		}
		&:last-child {
			width: 35%;
		}
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		width: 100%;

		.footer__row_top &:first-child {
			padding-bottom: 7rem;
			margin-bottom: 4.4rem;
			border-bottom: 1px solid var(--gray_color);
		}

		.footer__row_top &:last-child {
			padding-bottom: 6rem;
			border-bottom: 1px solid var(--gray_color);
		}

		.footer__row_bottom &:last-child {
			order: -1;
			margin-bottom: 4.4rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 100%;

		.footer__row_top &:first-child {
			padding-bottom: 3.4rem;
			margin-bottom: 4rem;
			border-bottom: 1px solid var(--gray_color);
		}

		.footer__row_top &:last-child {
			padding-bottom: 4.4rem;
			border-bottom: 1px solid var(--gray_color);
		}

		.footer__row_bottom &:last-child {
			order: -1;
			margin-bottom: 3.4rem;
		}
	}
}

.footer__content_w {
	width: 100%;
	margin-left: auto;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
		max-width: 39.6rem;
	}
}

.footer__lists_w {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
		margin: 0 -2rem -4rem 0;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		@include flex_block(row, nowrap, flex-start);
		margin: 0 -2rem -4rem 0;
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, wrap, flex-start);
		margin: 0 -2rem -3.4rem 0;
	}
}

.footer__list {
	@include bp($point_2, min) {
		padding: 0 2rem 4rem 0;
		max-width: 29.8rem;
		width: calc(100% / 3);

		&:last-child {
			max-width: 34rem;
		}
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		padding: 0 2rem 4rem 0;
		width: calc(100% / 3);
	}

	@include bp($point_4 - 1) {
		padding: 0 2rem 3.4rem 0;
		width: 50%;

		&:last-child {
			position: relative;
			width: 100%;

			padding-top: 3.6rem;

			&::after {
				position: absolute;
				top: 0;
				left: 0;

				width: calc(100% - 2rem);
				height: 1px;
				background-color: var(--gray_color);
				content: "";
			}
		}
	}
}

.footer__list_item,
.footer__list .menu-item {
	&:last-child {
		margin-bottom: 0;
	}

	@include bp($point_2, min) {
		margin-bottom: 2.4rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		margin-bottom: 2.4rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
	}
}

.footer__list_link,
.footer__list .menu-item a {
	font-weight: 400;
	color: var(--white_color);

	@extend %transition_opacity;

	&:hover,
	&:focus {
		text-decoration: none;
		opacity: 0.6;
	}

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}

.footer__title {
	line-height: 1.16;
	font-weight: 400;
	color: var(--white_color);

	@include bp($point_2, min) {
		margin-bottom: 7rem;
		max-width: 30.6rem;
		font-size: 3.6rem;
		letter-spacing: -0.02em;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		margin-bottom: 3.6rem;
		text-align: center;
		font-size: 2.8rem;
		letter-spacing: -0.025em;
	}

	@include bp($point_4 - 1) {
		// margin-bottom: 5.6rem;
		margin-bottom: 3rem;
		text-align: center;
		font-size: 1.9rem;
		letter-spacing: -0.03em;
	}
}

.footer__btn_w {
	width: 100%;

	@media (min-width: $point_4) and (max-width: $point_3) {
		@include flex_block(row, wrap, center);
	}

	@include bp($point_4 - 1) {
		@include flex_block(row, wrap, center);
	}
}

.footer__btn {
	@extend %btn_theme_2;
	@extend %btn_size_1;
}

.footer__btn_title {
	display: inline-block;

	&.footer__btn_title:not(:last-child) {
		// margin-right: 1rem;
		margin-right: 2rem;
	}
}

.footer__btn_icon {
	@extend %btn_icon_size_1;

	background-color: #fff;
	color: #000;
}

.footer__build_on {
	font-weight: 400;
	color: var(--white_color);

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 1.8rem;
		text-align: center;
	}

	@include bp($point_4 - 1) {
		font-weight: 500;
		font-size: 1.6rem;
		text-align: center;
	}
}

.footer__company_info {
	padding: 0 20rem 1.6rem 0;
	color: var(--gray_2_color);

	@include bp($point_2, min) {
		font-size: 2.2rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 1.8rem;
		text-align: center;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		text-align: center;
	}
}

.footer__icon_w {
	position: relative;
	display: inline-block;

	@include bp($point_4, min) {
		width: 6.8rem;
	}

	@include bp($point_4 - 1) {
		width: 4.8rem;
	}
}

.footer__company_info_w {
	margin: 0 -20rem -1.6rem 0;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
	}

	@include bp($point_3) {
		@include flex_block(column-reverse, nowrap, flex-start, center);
	}
}
