@import "ScssHelpers/helpers_all";

.showcases {
	@include bp($point_2, min) {
		padding: 23.1rem var(--gl-indent) 18rem;
	}

	@include bp($point_3, $point_4) {
		padding: 11rem var(--gl-indent) 13rem;
	}

	@include bp($point_4 - 1) {
		padding: 6.1rem var(--gl-indent) 6.4rem;
	}
}

.showcases__setion_title {
	@extend %h1;

	color: var(--primary_dark_color);

	@include bp($point_2, min) {
		margin-bottom: 12.6rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 3.2rem;
	}

	span {
		font-family: $font_2;
		color: var(--dot-color);
	}
}

.showcases__list {
	@include bp($point_2, min) {
		margin-bottom: 10.4rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 3.2rem;
	}
}

.showcases__list_in {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_2, min) {
		margin: 0 -5.4rem -11.4rem 0;
	}

	@include bp($point_3, $point_4) {
		margin: 0 -2.4rem -5rem 0;
	}
}

.showcases__item {
	@include bp($point_2, min) {
		width: 33.33%;
		padding: 0 5.4rem 11.4rem 0;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
		padding: 0 2.4rem 5rem 0;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		margin-bottom: 4rem;

		&:last-child {
			margin: 0;
		}
	}
}

.showcases__img {
	display: block;
	overflow: hidden;

	@include bp($point_2, min) {
		margin-bottom: 3.4rem;
	}

	@include bp($point_3) {
		margin-bottom: 2.6rem;
	}
}

.showcases__img_in {
	position: relative;
	display: block;

	@include bp($point_2, min) {
		transition: transform 0.6s;

		.showcases__img:hover &,
		.showcases__img:focus & {
			transform: scale(1.2);
		}
	}

	&:before {
		content: "";
		display: block;
		padding-bottom: 77.8%;
	}
}

.showcases__img_el {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.showcases__info {
	margin-bottom: 1.6rem;
}

.showcases__info_list {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_4, min) {
		margin: 0 0 -2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 0 -1rem 0;
	}
}

.showcases__info_item {
	position: relative;

	letter-spacing: -0.02em;
	color: var(--gray_color);

	@include bp($point_2, min) {
		padding: 0 1.8rem 2rem 0;
		margin-right: 1.6rem;
		font-size: 1.8rem;
	}

	@include bp($point_3, $point_4) {
		padding: 0 1.8rem 2rem 0;
		margin-right: 1.6rem;

		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.4rem 1rem 0;
		margin-right: 1rem;

		font-size: 1.3rem;
	}

	&:after {
		content: "";
		position: absolute;
		right: 0;
		top: 1rem;

		width: 0.4rem;
		height: 0.4rem;

		background-color: currentColor;
	}

	&:last-child {
		padding-right: 0;
		margin: 0;
		&:after {
			display: none;
		}
	}
}

.showcases__title {
	font-weight: 600;
	color: var(--primary_color);

	transition: color 0.3s;

	@include bp($point_2, min) {
		font-size: 3.2rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 2.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.8rem;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: #1da0df;
	}
}

.showcases__btn_wrap {
	@extend %global_flex_block_row_nowrap_center;
}

.showcases__btn {
	@extend %btn_size_2;
	@extend %btn_theme_6;
}
