%form_el_checkbox {
	position: relative;

	display: block;

	color: var(--white_color);

	cursor: pointer;
	-webkit-tap-highlight-color: transparent;

	@include bp($point_4, min) {
		padding-left: 3.8rem;
		font-size: 2rem;
	}
	@include bp($point_4 - 1) {
		padding-left: 3.2rem;
		font-size: 1.4rem;
	}

	&:before,&:after {
		position: absolute;
		top: 1px;
		left: 0;

		cursor:pointer;
		content: '';

		@include bp($point_4, min) {
			width: 2.4rem;
			height: 2.4rem;
		}
		@include bp($point_4 - 1) {
			width: 2rem;
			height: 2rem;
		}
	}

	&:before {
		border-radius: 5px;
		background-color: var(--white_color);
	}

	&:after {
		opacity: 0;
		background: url("../images/check_2.svg") 50% 50% no-repeat;
		background-size: 1.2rem 1rem;

		.form_checkbox__element:checked + & {
			opacity: 1;
		}
	}
}