.blog_hero {
	@include bp($point_2, min) {
		padding: 18.6rem var(--gl-indent) 10rem;
	}

	@include bp($point_3, $point_4) {
		padding: calc(var(--height-header) + 4.4rem) var(--gl-indent) 0;
	}


	@include bp($point_4 - 1) {
		padding: 4.4rem var(--gl-indent) 0;
	}


}

.blog_hero_heading {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center);

		&:not(:last-child) {
			margin-bottom: 5.6rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.blog_hero_title {
	@extend %h1;

	margin-right: auto;
	color: var(--primary_dark_color);

	@include bp($point_3) {
		margin-bottom: .8rem;
	}
}

.blog_hero_title__dot {
	@extend %dot;
}

%rectangle {
	@include bp($point_2, min) {
		width: 1.2rem;
		height: 1.2rem;
	}
	@include bp($point_3) {
		width: .8rem;
		height: .8rem;
	}
}

.blog_hero_decor {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	@include bp($point_2, min) {
		margin-bottom: 2.6rem;

		&.blog_hero_decor--mobile_mod {
			display: none;
		}
	}
	@include bp($point_3) {
		margin-bottom: 2.2rem;

		&.blog_hero_decor--desktop_mod {
			display: none;
		}
	}

	&::before,
	&::after {
		@extend %rectangle;
		content: '';
	}

	&::after {
		background-color: var(--primary_light_color);
	}

	&::before {
		background-color: var(--red_color);
	}
}

.blog_hero_decor__in {
	@extend %rectangle;

	background-color: var(--green_color);

	@include bp($point_2, min) {
		margin: 0 1.5rem;
	}
	@include bp($point_3) {
		margin: 0 1rem;
	}
}

.blog_hero_descr {
	width: 100%;

	@include bp($point_2, min) {
		max-width: 39.6rem;

		&:not(:first-child) {
			margin-left: 4rem;
		}
	}
}

.blog_hero_descr__in {
	max-width: 28rem;
	width: 100%;

	color: var(--gray_color);
	font-weight: 500;
	line-height: 1.5;
	letter-spacing: -.03em;

	@include bp($point_2, min) {
		font-size: 2.1rem;
	}
	@include bp($point_3) {
		font-size: 1.2rem;
	}
}
