@import "ScssHelpers/helpers_all";

.showcase_hero {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	padding: 0;

	color: var(--white_color);
	background-color: var(--primary_dark_color);

}

.showcase_hero__col {
	position: relative;

	min-height: var(--full-screen);

	&:nth-child(1) {
		@extend %global_flex_block_column_nowrap_flex-start_stretch;

		
		opacity: 0;
		
		@include bp($point_4, min) {
			padding: 14.6rem var(--gl-indent) 7rem;
		}

		@include bp($point_4 - 1) {
			padding: 4.2rem var(--gl-indent);
			min-height: 38.6rem;
		}
	}

	@include bp($point_2, min) {
		width: 50%;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.showcase_hero__bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;

	background-color: #4838a2;

	opacity: 0;
}

.showcase_hero__back {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	margin-bottom: 11.2rem;
	margin-left: .8rem;

	color: var(--white_color);
	
	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}

	&:hover, &:focus {
		text-decoration: none;
	}
}

.showcase_hero__back_icon {
	@extend %global_flex_block_row_nowrap_center_center;


	border-radius: 50%;
	background-color: #404769;
	color: var(--white_color);

	transition: all .3s;

	@include bp($point_2, min) {
		width: 4.6rem;
		height: 4.6rem;
		padding: 1.6rem;
		margin-right: 1rem;
	}

	@include bp($point_3) {
		width: 3.4rem;
		height: 3.4rem;
		padding: 1rem;
		margin-right: .8rem;
	}

	.showcase_hero__back:hover &, 
	.showcase_hero__back:focus & {
		background-color: var(--white_color);
		color: #404769;
	}
}

.showcase_hero__tag {
	
	color: var(--gray_color);
	
	@include bp($point_2, min) {
		margin-bottom: 4.6rem;
		font-size: 2.8rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 3rem;
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
		font-size: 1.3rem;
	}
}

.showcase_hero__title {
	@extend %h1;

	position: relative;

	color: var(--white_color);
	letter-spacing: -0.02em;

	span {
		font-family: $font_2;
	}

	@include bp($point_2, min) {
		margin-bottom: 4rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
	}
}

.showcase_hero__text {
	max-width: 67rem;
	
	line-height: 1.2;
	letter-spacing: -.01em;
	color: var(--white_color);
	
	@include bp($point_2, min) {
		margin-bottom: 4.6rem;
		font-size: 2.8rem;
	}

	@include bp($point_3, $point_4) {
		margin-bottom: 3rem;
		font-size: 2rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 2rem;
		font-size: 1.4rem;
		line-height: 1.3;
	}
}

.showcase_hero__link {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin-bottom: 2rem;

	letter-spacing: -0.02em;
	color: var(--blue_color_2);

	transition: color .3s;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3, $point_4) {
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}

	&:hover, &:focus {
		text-decoration: none;
		color: var(--orange_color);
	}
}

.showcase_hero__link_icon {
	@include bp($point_2, min) {
		width: 2.2rem;
		height: 2.2rem;
		margin-right: 1.4rem;
	}

	@include bp($point_3) {
		width: 1.6rem;
		height: 1.6rem;
		margin-right: 1rem;
	}
}

.showcase_hero__link_icon_img {
	@extend %contain_img;
}

.showcase_hero__info {

	margin-top: auto;

	&.showcase_hero__info--desktop {
		@include bp($point_4 - 1) {
			display: none;
		}
	}

	&.showcase_hero__info--mobile {
		width: 100%;
		padding: 4.2rem var(--gl-indent) 4.6rem;
		background-color: var(--white_color);

		@include bp($point_4, min) {
			display: none;
		}
	}
}

.showcase_hero__info_in {
	@extend %global_flex_block_row_wrap_flex-start;
	margin-bottom: -3rem;
}

.showcase_hero__info_item {
	padding: 0 2rem 3rem 0;
	@include bp($point_4, min) {
		width: 38%;
	}

	@include bp($point_4 - 1) {
		width: 50%;
	}
}

.showcase_hero__info_title {
	margin-bottom: 1.2rem;

	font-weight: 700;
	
	@include bp($point_2, min) {
		font-size: 2rem;
		color: var(--white_color);
	}

	@include bp($point_3, $point_4) {
		font-size: 1.6rem;
		color: var(--white_color);
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		color: var(--primary_dark_color);
	}
}

.showcase_hero__info_subitem {
	@include bp($point_2, min) {
		font-size: 2rem;
		color: var(--gray_color);
	}

	@include bp($point_3, $point_4) {
		font-size: 1.6rem;
		color: var(--gray_color);
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		color: var(--primary_dark_color);
	}
}

// slider

.showcase_hero__slider {
	position: relative;

	z-index: 2;
}

.showcase_hero__slider_item {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	height: var(--full-screen) !important;
	padding-bottom: 4rem;
}

.showcase_hero__slider_item_in {
	width: 100%;
	// opacity: 0;

	@include d3();
}

.showcase_hero__slider_img {
	display: block;
	height: calc(var(--full-screen) * .6);
	width: 82%;
	margin: 0 auto;

	// opacity: 0;
	// @include d3();

	&.showcase_hero__slider_img--lg_mod {
		width: 125%;
		margin-right: -42%;
		
		@include bp($point_2, min) {
			margin-left: 14rem;
		}
	}
}

.showcase_hero__slider_img_in {
	@extend %contain_img;
}

.showcase_hero__controls {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	position: absolute;
	left: 0;
	width: 100%;

	
	opacity: 0;
	z-index: 3;
	
	@include bp($point_2, min) {
		padding: 0 var(--gl-indent) 0 7.4rem;
		bottom: 7.4rem;
	}

	@include bp($point_3, $point_4) {
		padding: 0 var(--gl-indent);
		bottom: 5rem;
	}

	@include bp($point_4 - 1) {
		bottom: 3.8rem;
	}
}

.showcase_hero__progress {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	margin-left: auto;

	
	@include bp($point_2, min) {
		font-size: 3.6rem;
		font-weight: 500;
	}

	@include bp($point_3, $point_4) {
		width: 100%;
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;
		font-size: 1.3rem;
	}
}

.showcase_hero__progress_label {
	min-width: 3rem;
	line-height: .9;
	text-align: center;
}

.showcase_hero__arrows {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	@include bp($point_2) {
		display: none;
	}
}

.showcase_hero__arrow {
	@extend %global_flex_block_row_nowrap_center_center;

	width: 4.6rem;
	height: 4.6rem;
	margin-right: 2rem;

	border-radius: 50%;
	background-color: var(--white_color);
	color: var(--primary_dark_color);

	transition: all .3s;

	cursor: pointer;

	&:last-child {
		margin: 0;
	}

	&:hover, &:focus {
		background-color: var(--primary_dark_color);
		color: var(--white_color);
	}
}

.showcase_hero__arrow_icon {
	width: 1rem;
	height: 1.6rem;
}

.showcase_hero__progress_dots {
	@include bp($point_2, min) {
		&:before {
			content: '/';
		}
	}

	@include bp($point_3) {
		@include flex_block(row, nowrap, flex-start, center);
		
		position: relative;

		flex-grow: 1;
		height: 1px;

		flex-grow: 1;
		height: 0.2rem;
	
		background-color: var(--gray_color);
	
		.swiper-pagination-progressbar-fill {
			flex-grow: 1;
			height: inherit;
	
			transform-origin: left top;
			background-color: var(--white_color);
		}
	}
}