$filters_border: 1px solid var(--light_blue_2);

.blog_filters {
	position: relative;
	min-height: 7.8rem;


	&::before {
		z-index: 1;
		position: absolute;
		left: 50%;
		top: 0;
		transform: translateX(-50%);

		width: 100vw;
		height: 100%;

		background-color: var(--white_color);
		border-top: $filters_border;
		border-bottom: $filters_border;
		content: '';
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, stretch);
	}
	@include bp($point_3) {
		width: 100vw;
		margin-left: calc(var(--gl-indent) * -1);
	}
}

// %filters_row_wrap {

// 	@include bp($point_3) {

// 		@include flex_block(row, nowrap, center, flex-start);
// 	}
// }

%filters_row {
	width: 100%;

	overflow-x: auto;

	@include bp($point_2, min) {

		&::-webkit-scrollbar {
			height: .8rem;
		}
		&::-webkit-scrollbar-track {
			background-color: transparent;
		}
		&::-webkit-scrollbar-thumb {
			background-color: var(--blue_color);
			// border-radius: .3rem;
		}
	}

	@include bp($point_3) {
		padding: 0 var(--gl-indent);
	}
}

.blog_filters_main_w {
	// @extend %filters_row_wrap;
	position: relative;
	z-index: 2;
	width: 100%;

	@include bp($point_2, min) {
		max-width: 65%;
	}

	@include bp($point_3) {
		position: relative;

		&::after,
		&::before {
			z-index: 3;
			position: absolute;
			top: 1px;

			width: 2.4rem;
			height: 5.8rem;
			content: '';
		}

		&::before {
			left: 0;
			background-image: linear-gradient(to right, var(--white_color) 0%, rgba(255, 255, 255, 0) 100%);
		}

		&::after {
			right: 0;
			background-image: linear-gradient(to left, var(--white_color) 0%, rgba(255, 255, 255, 0) 100%);
		}

		&:not(:last-child) {
			border-bottom: $filters_border;
		}
	}
}

.blog_filters_main {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	@extend %filters_row;
}

.blog_filters_main__item {
	&:not(:last-child) {

		@include bp($point_2, min) {
			margin-right: 8.6rem;
		}
		@include bp($point_3) {
			margin-right: 5.2rem;
		}
	}
}

%filters_link {
	@extend %global_flex_block_row_nowrap_flex-start_center;

	font-weight: 500;
	color: var(--gray_color);
	font-family: $biotif_font;
	white-space: nowrap;

	transition: color .3s;

	@include bp($point_2, min) {
		min-height: 7.8rem;
		font-size: 2.1rem;
		letter-spacing: -.04em;
	}
	@include bp($point_3) {
		min-height: 6rem;
		font-size: 1.6rem;
		letter-spacing: -.02em;
	}
}

.blog_filters_main__link {
	@extend %filters_link;

	padding-top: .4rem;

	&:hover,
	&:focus {
		color: var(--primary_color);
		text-decoration: none;
	}
}

// .blog_filters_sub_w {
// 	@extend %filters_row_wrap;


// }

.blog_filters_sub {
	@extend %global_flex_block_row_nowrap_flex-start_center;
	@extend %filters_row;

	position: relative;
	z-index: 2;

	@include bp($point_3) {
		min-height: 6.6rem;
	}

	&:not(:first-child) {
		@include bp($point_2, min) {
			padding-left: 5rem;
			margin-left: 6rem;
			border-left: $filters_border;
		}
	}
}

.blog_filters_sub__item {

	&:not(:last-child) {

		border-right: $filters_border;

		@include bp($point_2, min) {
			padding-right: 5.8rem;
			margin-right: 5.4rem;
		}
		@include bp($point_3) {
			padding-right: 1.4rem;
			margin-right: 1.6rem;
		}
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.blog_filters_sub__link {
	@extend %filters_link;

	@include bp($point_3) {
		min-height: 3.6rem;
		width: 100%;
	}

	&:hover,
	&:focus {
		color: var(--orange_color);
		text-decoration: none;
	}
}

.blog_filters_sub__icon {
	margin-right: 1.2rem;

	@include bp($point_2, min) {
		width: 2.6rem;
		height: 3.4rem;
	}
	@include bp($point_3) {
		width: 2.4rem;
		height: 2.4rem;
	}
}

.blog_filters_sub__link_title {
	padding-top: .4rem;
}