@import "reset";
@import "family";
@import "print";
@import "fonts";

//------------------------------------------------------------base styles
:root {
	--white_color: #fff;
	--black_color: #000;
	--light_color: #fafbfc;
	--orange_color: #fe3b1f;
	--red_color: #FB3B1E;

	--gray_color: #7e92b4;
	--gray_2_color: #a5adc6;
	--gray_3_color: #e8eff7;
	--gray_4_color: #dee6f4;

	--dot-color: #cad4e6;

	--primary_color: #254b88;
	--primary_dark_color: #242b4d;
	--primary_light_color: #1da0df;

	--blue_color: #353c5e;
	--blue_color_2: #6598eb;
	--blue_color_3: #31baff;
	--light_blue: #ecf2f7;
	--light_blue_2: #d4dff4;
	--light_blue_3: #cad8f2;

	--green_color: #2bb451;
	--green_color_2: #30c75f;

	--border_grey_color: #dee6f5;
	--code_bg_color: #eaecf0;

	--color_text_base: var(--black_color);
	--color_link_base: #4aa3df;
	--body_bg: var(--white_color);
	--menu-bg: #0e1e45;

	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-footer: 65.2rem;

	--height-cookie-mob: 7.2rem;

	::-webkit-scrollbar {
		width: 1rem;
		background-color: #e8eff7;
	}

	::-webkit-scrollbar-thumb {
		background-color: rgba(#7e92b4, .5);
	}

	@include bp($point_2, min) {
		--height-header: 12.8rem;
		--gl-indent: 9.2rem;
		--testimonial-info-col-width: 24.6rem;
		--testimonial-client-text-height: 28rem;
		--testimonial-client-footer-height: 8.2rem;
	}

	@include bp($point_3, $point_4) {
		--height-header: 6rem;
		--gl-indent: 3.2rem;
		--testimonial-info-col-width: 19.6rem;
		--testimonial-client-text-height: 25rem;
		--testimonial-client-footer-height: 8rem;
	}

	@include bp($point_4 - 1, $point_6) {
		--testimonial-client-text-height: 16rem;
	}

	@include bp($point_4 - 1) {
		--height-header: 7.2rem;
		--gl-indent: 2rem;
		--testimonial-client-footer-height: 7.4rem;
	}

	@include bp($point_6 - 1) {
		--testimonial-client-text-height: 23rem;
	}
}

html {
	height: 100%;

	font-weight: 400;
	line-height: 1.33;
	font-family: $biotif_font;

	// font-size: 10px !important;

	@include bp($point_2, min) {
		font-size: calc(100vw / 1920 * 10);
	}

	@include bp($point_3) {
		font-size: 10px;
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;
	width: 100vw;
	// min-height: 150vh;

	font-size: $font_size_base;
	color: var(--color_text_base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body_bg);
	overflow-x: hidden;

	opacity: 0;

	@supports (display: grid) {
		display: block;
	}

	&.body--active_menu {
		overflow: hidden;
	}

	&.body--bg_mod {
		background-color: var(--light_color);
	}

	&.body--clipped {
		overflow: hidden;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;

	.body--cookie_mod & {
		@include bp($point_4 - 1) {
			padding-top: var(--height-cookie-mob);
		}
	}

	.body--mobile_cookie & {
		@include bp($point_4 - 1) {
			transition: transform ease 0.3s;
			transform: translateY(calc(-1 * var(--height-cookie-mob)));
		}
	}
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;

	@include bp($point_4 - 1) {
		padding: var(--height-header) 0 0;
	}

	.body--active_menu & {
		@include bp($point_2, min) {
			// overflow-y: scroll;

			&::-webkit-scrollbar {
				opacity: 0;
			}
		}
	}
}

a {
	text-decoration: none;
	color: var(--color_link_base);
	transition: all .2s linear;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	padding: 0;
	margin: -1px;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}

//------------------------------------------------------------base styles###
