@import 'ScssHelpers/_helpers_all.scss';

.team_hero {
	@include bp($point_4, min) {
		@include flex_block(row, wrap, flex-start, center);

		height: 100vh;
	}
}

.team_hero__text {
	@include bp($point_2, min) {
		padding: calc(var(--height-header) + 6rem) var(--gl-indent);
	}

	@include bp($point_3, $point_4) {
		padding: calc(var(--height-header) + 3rem) var(--gl-indent) 3rem;
	}

	@include bp($point_4, min) {
		width: 50%;
	}

	@include bp($point_4 - 1) {
		padding: 6.1rem var(--gl-indent) 4rem;
	}
}

.team_hero__title {
	@extend %h1;

	letter-spacing: -0.05em;
	color: var(--primary_dark_color);


	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 9rem;
		}

		@include bp($point_3, $point_4) {
			margin-bottom: 3rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2.7rem;
		}
	}
}

.team_hero__title_dot {
	font-family: Arial, sans-serif;
	color: var(--dot-color);
}

.team_hero__description {
	@extend %base_text_2;

	letter-spacing: -0.03em;

	@include bp($point_4, min) {
		max-width: 45rem;
	}

	@include bp($point_4 - 1) {
		max-width: 25rem;
	}

	a {
		@extend %transition_color;

		color: var(--blue_color_2);

		&:hover {
			text-decoration: none;
			color: var(--orange_color);
		}

		&:focus {
			text-decoration: none;
		}
	}
}

.team_hero__img_wrap {
	@include bp($point_4, min) {
		width: 50%;
		height: 100%;
	}

	@include bp($point_4 - 1) {
		position: relative;

		padding-top: 100%;
		width: 100%;
	}
}

.team_hero__picture {
	display: block;
	width: 100%;
	height: 100%;

	@include bp($point_4 - 1) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.team_hero__img {
	@extend %cover_img;
}