@import "ScssHelpers/_helpers_all.scss";

:root {
	--header-primary-color: var(--white_color);
	--header-secondary-color: var(--blue_color);
	--header-accent-color: var(--primary_color);
	--menu-anim-enter: transform 0.14s ease-in-out;
	--menu-anim-out: transform 0.14s ease-in-out 0.1s;
}

//------------------------------------------------------------layout
.header {
	@extend %global_flex_block_row_wrap_flex-start_center;

	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;

	font-size: 1rem;

	will-change: transform;

	@include z-index(header);

	// .body--active_menu & {
	// 	@include bp($point_2, min) {
	// 		overflow-y: scroll;

	// 		&::-webkit-scrollbar {
	// 			opacity: 0;
	// 		}
	// 	}
	// }

	// .body--mobile_cookie & {
	// 	transition: transform ease .3s;

	// 	@include bp($point_4 - 1) {
	// 		transform: translateY(var(--height-cookie-mob));
	// 	}
	// }

	@include bp($point_2, min) {
		padding: 1rem 4rem 1rem 7rem;
	}
	@include bp($point_3, $point_4) {
		padding: 1rem 2rem 1rem 3.2rem;
	}
	@include bp($point_4, min) {
		position: absolute;
	}
	@include bp($point_4 - 1) {
		position: fixed;
		padding: 1rem var(--gl-indent);
		background-color: var(--primary_dark_color);
		border-bottom: 1px solid rgba(#7e92b4, 0.5);

		.body--cookie_mod & {
			transform: translateY(var(--height-cookie-mob));
		}

		.body--mobile_cookie & {
			transition: transform ease 0.3s;
			transform: translateY(0);
		}
	}
}

.header_in {
	@extend %global_flex_block_row_wrap_flex-start_center;

	width: 100%;
	overflow: hidden;
}

.header_logo {
	position: relative;
	display: block;

	margin-right: 2rem;

	@include bp($point_2, min) {
		width: 12.7rem;
		height: 3.2rem;
	}
	@include bp($point_3, $point_4) {
		width: 7.2rem;
		height: 1.8rem;
	}
	@include bp($point_4 - 1) {
		width: 8.5rem;
		height: 2.2rem;
	}
}

.header_logo__img {
	@extend %contain_img;

	opacity: 1;
	transition: opacity .3s ease-in-out .3s;

	.header--color_mod & {
		@include bp($point_4, min) {
			opacity: 0;
		}
	}

	&.header_logo__img--fill_2_state {
		position: absolute;
		left: 0;
		top: 0;
		opacity: 0;

		.header--color_mod & {
			@include bp($point_4, min) {
				opacity: 1;
			}
		}

		.body--active_menu & {
			@include bp($point_4, min) {
				opacity: 0;
			}
			transition: opacity .3s ease-in-out;
		}
	}

	.body--active_menu & {
		@include bp($point_4, min) {
			opacity: 1;
		}
		transition: opacity .3s ease-in-out;
	}
}

.color_themes {
	@extend %global_flex_block_row_nowrap_flex-start;

	margin-left: auto;
}

.color_themes__item {
	@extend %global_flex_block_row_nowrap_center_center;

	@include bp($point_2, min) {
		width: 4.4rem;
		height: 4.4rem;


		&.color_themes__item--light_mod {
			background-color: var(--green_color);
			color: var(--white_color);

			.body--dark_mod & {
				background-color: tomato;
			}
		}

		&.color_themes__item--dark_mod {
			background-color: var(--border_grey_color);
			color: var(--primary_dark_color);

			.body--dark_mod & {
				color: var(--white_color);
				background-color: var(--green_color);
			}
		}
	}

	@include bp($point_3) {
		color: var(--white_color);

		&.color_themes__item--dark_mod {
			display: none;
		}
	}
}

.color_themes__icon {
	width: 2.2rem;
	height: 2.2rem;
}

.header_menu_trigger {
	position: relative;

	margin-left: auto;

	.color_themes + & {
		@include bp($point_2, min) {
			margin-left: 15.8rem;
		}

		@include bp($point_3) {
			margin-left: 3rem;
		}
	}

	cursor: pointer;

	@include bp($point_2, min) {
		width: 4.4rem;
		height: 4.4rem;
	}
	@include bp($point_3, $point_4) {
		width: 2.4rem;
		height: 2.4rem;
	}
	@include bp($point_4 - 1) {
		width: 2rem;
		height: 2rem;
	}

	&::after {
		z-index: 2;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: var(--header-primary-color);
		transition: background-color 0.2s ease 0.1s;

		@include bp($point_4, min) {
			content: "";
		}

		.header--color_mod & {
			background-color: var(--header-secondary-color);
		}
	}

	body.body--active_menu & {
		&::after {
			background-color: var(--header-secondary-color);
		}
	}
}

.header_menu_trigger__title {
	@extend %global_flex_block_row_wrap_flex-start_center;

	position: absolute;
	right: 100%;
	top: 0;

	height: 100%;
	padding: 0 2.4rem;

	font-size: 2rem;
	color: var(--header-primary-color);
	white-space: nowrap;

	transform: translateX(101%);
	background-color: var(--header-secondary-color);

	transition: var(--menu-anim-out);
	will-change: transform;

	body.body--active_menu.body--active_menu & {
		transform: translateX(101%);
		transition: transform 0.2s ease;
	}

	@include bp($point_2, min) {
		.header_menu_trigger:hover & {
			transform: translateX(0);
			transition: var(--menu-anim-enter);
		}
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.header_menu_trigger__icon {
	z-index: 3;
	position: relative;

	display: block;
	width: 100%;
	height: 100%;

	transition: var(--menu-anim-out), opacity 0.2s ease 0.1s;

	@include bp($point_2, min) {
		padding: 1.2rem;
	}
	@include bp($point_3, $point_4) {
		padding: 0.7rem;
	}

	&.header_menu_trigger__icon--open_mod {
		@include bp($point_2, min) {
			.header_menu_trigger:hover & {
				transform: rotateZ(135deg);
				transition: var(--menu-anim-enter), opacity 0.2s ease 0.1s;
			}
		}

		@include bp($point_4, min) {
			color: var(--header-accent-color);
		}
		@include bp($point_4 - 1) {
			color: var(--header-primary-color);
		}

		body.body--active_menu & {
			opacity: 0;
		}

		.header--color_mod & {
			color: var(--white_color);
		}
	}

	&.header_menu_trigger__icon--close_mod {
		position: absolute;

		color: var(--header-primary-color);

		transition: var(--menu-anim-out);
		transform: scale(0);

		@include bp($point_2, min) {
			.header_menu_trigger:hover & {
				body.body--active_menu & {
					transform: scale(1) rotateZ(90deg);
				}
			}
		}
		@include bp($point_4, min) {
			left: 0;
			top: 0;

			width: 100%;
			height: 100%;
		}
		@include bp($point_4 - 1) {
			left: 50%;
			top: 50%;

			margin: -0.8rem 0 0 -0.8rem;

			width: 1.6rem;
			height: 1.6rem;
		}

		body.body--active_menu & {
			opacity: 1;
			transform: scale(1);
		}
	}
}

